body {
  font-family: Montserrat, sans-serif;
}

h2, h3, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  cursor: pointer;
}

.modal-close-btn {
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

:root {
  --green-bg-color: #84a178;
  --green-products-bg-color: #9cbe8f;
  --yellow-products-bg-color: #efd478;
